import React from "react";

function Header() {
  const hour = Math.floor(Math.random() * 25);

  let greeting;

  let customStyle = {};

  if (hour < 12) {
    greeting = `Buenos dÃ­as, son las ${hour} Horas`;
    customStyle.color = "red";
  } else {
    if (hour < 18) {
      greeting = `Buenas tardes, son las ${hour} Horas`;
      customStyle.color = "green";
    } else {
      greeting = `Buenas noches, son las ${hour} Horas`;
      customStyle.color = "blue";
    }
  }

  return (
    <h1 className="header" style={customStyle}>
      {" "}
      {greeting}{" "}
    </h1>
  );
}

export default Header;
